import React from "react"
import { Link } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"

const Word = () => (
  <Layout>
    <SEO title="Vad är Nolimit City?" />
    <div className="content-header">
      <h1>Vad är Nolimit City?</h1>
      <p>En spelleverantör som har sin bas i Stockholm och som fokuserar framförallt på casinospel i mobilen. Känd för speltitlar som <Link to="oktoberfest">Oktoberfest</Link>, <Link to="/wixx">Wixx</Link> och <Link to="/owls">Owls</Link>.</p>
      <Link to="/" className="back-button">Tillbaka till startsidan</Link>
    </div>
  </Layout>
)

export default Word
